<template>
    <div class="table">
        <div class="loading" v-if="tableLoading">
            <md-activity-indicator :size="35" vertical class="md-activity-indicator-css">加载中...
            </md-activity-indicator>
        </div>
        <div class="totalList" v-else>
            <table-item v-for="(item, index) in tableData" :key="index" :data="item"></table-item>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mand-mobile'
    import tableItem from '../components/tableItem'
    export default {
        name: "table",
        components: {
            tableItem
        },
        data() {
            return {
                tableLoading: false,
                tableData: []
            }
        },
        methods: {
            getTotalData() {
                this.tableLoading = true;
                this.$http.get('/api/getTotalItems?sortType=1').then(res => {
                    this.tableData = res.data.data;
                    this.tableLoading = false;
                }).catch(() => {
                    this.tableLoading = true;
                    Toast.failed('数据获取失败')
                })
            }
        },
        mounted() {
            this.getTotalData();
        }
    }
</script>

<style scoped>
    .loading .md-activity-indicator-css {
        margin-top: 40%;
        font-size: 0.4rem;
    }
    /deep/.totalList .md-button {
        padding-right: 0;
    }
</style>
